import React, {useEffect, useState} from "react";
import {observer} from "mobx-react";

export interface RepeatedEmailConfirmedProps {
    onControl: () => void;
}

export const RepeatedEmailConfirmed: React.ComponentType<RepeatedEmailConfirmedProps> = observer(({onControl}) => {
    const [state, setState] = useState({
        email: ''
    });
    const [error, setError] = useState('');
    const handleSubmit = () => {
        setError('');
        let pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
        if (!pattern.test(state.email)) {
            return setError('–í–æ–∑–º–æ–∂–Ω–æ –≤—ã –æ—à–∏–±–ª–∏—Å—å –≤ —É–∫–∞–∑–∞–Ω–∏–∏ –ø–æ—á—Ç–æ–≤–æ–≥–æ —Å–µ—Ä–≤–∏—Å–∞')
        }
        onControl();
    }

    useEffect(() => setError(''), [state]);

    return (
        <div className="confirmed-container repeated-email">
            <h1>–ú–Ω–µ –Ω–µ –ø—Ä–∏—à–ª–æ –ø–∏—Å—å–º–æ</h1>
            <h2>–ü–∏—Å—å–º–æ –º–æ–∂–µ—Ç –ø—Ä–∏–π—Ç–∏ —Å –∑–∞–¥–µ—Ä–∂–∫–æ–π –≤ 5-10 –º–∏–Ω—É—Ç. <br />
                –¢–∞–∫–∂–µ –ø—Ä–æ–≤–µ—Ä—å—Ç–µ —Ä–∞–∑–Ω—ã–µ –ø–∞–ø–∫–∏ –ø–æ—á—Ç–æ–≤–æ–≥–æ —è—â–∏–∫–∞ (–∞–∫—Ç—É–∞–ª—å–Ω–æ –¥–ª—è gmail.com) –∏ –ø–∞–ø–∫—É <br />
                "–°–ø–∞–º".–ï—Å–ª–∏ –ø–∏—Å—å–º–æ –≤—Å–µ –∂–µ –Ω–µ –ø—Ä–∏—à–ª–æ, –ø–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–æ–ø—ã—Ç–∫—É –∏–ª–∏ –Ω–∞–ø–∏—à–∏—Ç–µ –æ–± —ç—Ç–æ–º –≤ <br />
                —Ç–µ—Ö.–ø–æ–¥–¥–µ—Ä–∂–∫—É <a href="mailto:support@livedune.ru"
                                 className="link">support@livedune.ru</a> –∏ –º—ã –∞–∫—Ç–∏–≤–∏—Ä—É–µ–º –≤–∞—à –∞–∫–∫–∞—É–Ω—Ç. </h2>
            <div className="confirmed-container__input-group">
                <input className={error ? 'danger' : ''} type="email" value={state.email} name="email"
                       placeholder="Email"
                       onChange={(e) => setState({...state, email: e.currentTarget.value})} />
                <div className="text-danger">{error}</div>
            </div>
            <div className="confirmed-container__submit-group">
                <button onClick={handleSubmit} className="btn btn-blue">–û—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–Ω–æ–≤–æ</button>
                <button className="link link-grey">–û—Ç–º–µ–Ω–∏—Ç—å</button>
            </div>
        </div>
    )
})
